<template>
    <div class="stepThree">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="headTitle">{{ userType === 'T' ? '教练认证' : '武馆认证' }}</span>
            <span class="back"></span>
        </div>
        <div class="upLoad">
            <span class="videoImg" v-show="hasCover">
                <img :src="videoCover">
            </span>
            <label class="getImg" for="myfile" @click="avatar" id="preView" v-show="!hasCover"></label>
            <input type="file" accept="video/mp4" name="myfile" ref="myfiles" style="display: none" @change="getCover">
        </div>
        <span class="addTitle">添加演练视频</span>
        <button class="nextBtn" @click="registe">提交认证资料</button>
        <span class="notice">温馨提示：提交认证资料成功后，系统将在3个工作日内提交反馈</span>
    </div>
</template>

<script>
import { getCover, uploadVideo } from '../../api/getdata'
// import { getCookie, setCookie } from '../../../static/js/cookieUtil'
import { Toast, MessageBox, Indicator } from 'mint-ui'
export default {
  name: 'coachStep3',
  data () {
    return {
      userType: '',
      videoCover: '',
      hasCover: false
    }
  },
  mounted () {
    this.userType = localStorage.getItem('userType')
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    avatar () {
      this.$refs.myfiles.click()
    },
    getCover () {
      if (this.$refs.myfiles.length !== 0) {
        var video = new FormData()
        video.append('file', this.$refs.myfiles.files[0])
        video.append('scene', 'releaseVideoCoach')
        let config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        Indicator.open({
          text: '视频上传中',
          spinnerType: 'snake'
        })
        let data = getCover(video, config)
        data.then(res => {
          console.log(res.data.fullFile)
          this.videoCover = res.data.fullFile
          localStorage.setItem('videoCover', res.data.relaFile)
          this.hasCover = true
          Indicator.close()
        })
      }
    },
    registe () {
      if (this.$refs.myfiles.length !== 0) {
        var video = new FormData()
        Indicator.open({
          text: '资料提交中',
          spinnerType: 'snake'
        })
        video.append('relaFile', localStorage.getItem('videoCover'))
        video.append('videoName', '认证视频')
        video.append('videoNote', '认证视频')
        video.append('open', '0')
        let config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        let data = uploadVideo(video, config)
        data.then(res => {
          console.log(res)
          if (res.status === '0') {
            // console.log(res.data)
            Indicator.close()
            Toast({
              message: '上传成功',
              duration: 1000
            })
            this.$router.push('/home/enroll/introduce')
          } else {
            Toast({
              message: '上传失败，请重新上传',
              duration: 1000
            })
            Indicator.close()
          }
        })
      } else {
        MessageBox({
          message: '请先选择上传视频',
          duration: 1000
        })
      }
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.5rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .getImg {
    display: block;
    width: 3.73333rem;
    height: 4.73333rem;
    margin: 0 auto;
    margin-top: 4.16rem;
    background: #f5f5f5;
    border: none;
    outline: none;
    background-image: url('../../assets/img/addUpload.png');
    background-position: center;
    background-size: 1.2rem 1.2rem;
    background-repeat: no-repeat;
  }

  .nextBtn {
    width: 8.77333rem;
    height: 1.33333rem;
    border-radius: 0.15rem;
    background: #ffd200;
    border: none;
    outline: none;
    margin-top: 2.506667rem;
    font-weight: 400;
  }

  .addTitle {
    display: inline-block;
    margin-top: 0.906667rem;
  }

  .notice {
    width: 8.77333rem;
    display: inline-block;
    margin-top: 0.32rem;
    text-align: start;
    font-size: 0.346667rem;
    color: #666666;
  }

  .videoImg {
    display: inline-block;
    margin-top: 4.16rem;
    width: 6rem;
  }
</style>



